import React, { useState, useEffect } from "react";
import "../../src"
import user from "../APIs/user";
import cats from "../APIs/cats";
import NameBtn from "../components/NameBtn/index"
import { Container, Col, Row } from "./Grid/index";
import Jumbotron from "./Jumbotron";




function MainFunctional() {
  const [data, setData] = useState([]);
  const [cat, setCat] = useState([])
  const [searchTerm, setSearchTerm] = useState("")

  this.state={
    names: []
}

addName = () => {

    this.setState({names: [...this.state.names, item.name.first]})
}
  
  useEffect(() => {
    user.getRandomUserNames().then((response) => {
      setData(response.data.results);
    });
    cats.getRandomCat().then((response) => {
      setCat(response.data[0]);
    });
  
  }, []);

  return (
    <Container>
      <div className="App">
        <Jumbotron  src={`${process.env.PUBLIC_URL}/imgs/capture.png`} />
        <Row>
          <Col size= "lg-1 mx-auto">
            <img className="catImg" src={cat.url}/>
          </Col>
        </Row>

        <Row>
          <div>
            <h1 className="header">
              Pick 3 names!
            </h1>
          </div>
        </Row>

        <Row>
        <Col size= "lg-4 mx-auto"><h3 className="choices">1.){names[0]}</h3></Col>
        <Col size= "lg-4 mx-auto"><h3 className="choices">2.)</h3></Col>
        <Col size= "lg-4 mx-auto"><h3 className="choices">3.)</h3></Col>
        </Row>

        <Row>
          <Col size= "-6 "><button  style={{marginTop: "30px"}}type="button" className=" clear btn btn btn-dark btn-lg">Name it!</button></Col>
          <Col size= "xs-3 "> <button  style={{marginTop: "35px"}}type="button" className=" clear btn btn btn-danger btn-sm">Clear Names</button></Col>
        </Row>

        <Row>
        <input
            type="text"
            id="header-search"
            placeholder="Filter Names..."
            onChange={event=> {setSearchTerm(event.target.value)}}
            name="s" 
        />
        </Row>
        <Row>
          <Col size= "lg-2" ></Col>
          <Col className="names" size="lg-9 xl-8 mx-auto">
          {data.filter((val) => {
          let firstName = JSON.stringify(val.name.first)
          if (searchTerm == "") {
            return val
          }else if (firstName.toLowerCase().includes(searchTerm.toLowerCase())) {
            return val
          }
        }).map((item,index) => {
          return  <NameBtn 
                  key={index} 
                  onClick={addName()}
                  name={item.name.first}/>
        })}
          </Col>
        </Row>
      </div>
    </Container>
    
  );
}

export default MainFunctional;
